<template>
	<view>
		<StatusBar title="帮助信息编辑" bgColor="light bg-brown"></StatusBar>
		<view>
			<form>
				<view class="cu-form-group margin-top">
					<view class="title">标题</view>
					<input placeholder="这里输入标题" name="input"></input>
				</view>

				<view class="cu-bar bg-white margin-top">
					<view class="action">
						图片上传
					</view>
					<view class="action">
						{{imgList.length}}/4
					</view>
				</view>
				<view>
					<button @click="chooseWord">选择word文档</button>
				</view>
				<view class="cu-form-group">
					<view class="grid col-4 grid-square flex-sub">
						<view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage"
							:data-url="imgList[index]">
							<image :src="imgList[index]" mode="aspectFill"></image>
							<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
								<text class='cuIcon-close'></text>
							</view>
						</view>
						<view class="solids" @tap="ChooseImage" v-if="imgList.length<4">
							<text class='cuIcon-cameraadd'></text>
						</view>
					</view>
				</view>
				<view class="cu-form-group margin-top">
					<view class="editor_toolbox">
						<i class="iconfont icon-undo" data-method="undo" @tap="edit" />
						<i class="iconfont icon-redo" data-method="redo" @tap="edit" />
						<i class="iconfont icon-img" data-method="insertImg" @tap="edit" />
						<i class="iconfont icon-video" data-method="insertVideo" @tap="edit" />
						<i class="iconfont icon-link" data-method="insertLink" @tap="edit" />
						<i class="iconfont icon-text" data-method="insertText" @tap="edit" />
						<i class="iconfont icon-clear" @tap="clear" />
						<i class="iconfont icon-save" @tap="save" />
					</view>
					<view style="padding-bottom: 250px;">
						<mp-html ref="article" container-style="padding:20px" :content="content"
							domain="https://6874-html-foe72-1259071903.tcb.qcloud.la/demo" :editable="editable" />
					</view>
				</view>

			</form>
		</view>
	</view>
</template>

<script>
	import StatusBar from '../components/statusBar.vue'
	import {
		getHelpDetail
	} from "@/api/help/help.js"
	import {
		baseUrl
	} from "@/common/request.js"
	// import mpHtml from 'mp-html/dist/uni-app/components/mp-html/mp-html'
	export default {
		components: {
			StatusBar,
			// mpHtml
		},
		onLoad(option) {
			this.id = option.id;
			console.log(this.id);
			//获取详情信息
			getHelpDetail({
				'id': this.id
			}, res => {
				console.log("getHelpDetail返回数据", res)

			})

		},
		data() {
			return {
				editable: true,
				html: '<div> hello world </div>',
				readOnly: false,
				id: 1,
				content: '<div> hello world </div>',
				imgList: []
			}
		},
		onReady() {
			/**
			 * @description 设置获取链接的方法
			 * @param {String} type 链接的类型（img/video/audio/link）
			 * @param {String} value 修改链接时，这里会传入旧值
			 * @returns {Promise} 返回线上地址
			 *   type 为音视频时可以返回一个数组作为源地址
			 *   type 为 audio 时，可以返回一个 object，包含 src、name、author、poster 等字段
			 */
			this.$refs.article.getSrc = (type, value) => {
				return new Promise((resolve, reject) => {
					if (type === 'img') {
						uni.showActionSheet({
							itemList: ['本地选取', '远程链接'],
							success: res => {
								// 本地选取
								if (res.tapIndex === 0)
									uni.chooseImage({
										count: 1,
										success: res => {
											/* 实际使用时，这里需要上传到服务器后返回
											uni.showLoading({
												title: '上传中'
											})
											uni.uploadFile({
												url: 'xxx', // 接口地址
												filePath: res.tempFilePaths[0],
												name: 'xxx',
												success(res) {
													resolve(res.data.path) // 返回线上地址
												},
												fail: reject,
												complete: uni.hideLoading
											})*/
											resolve(res.tempFilePaths[0])
										},
										fail: reject
									})
								// 远程链接
								else {
									this.callback = {
										resolve,
										reject
									}
									this.$set(this, 'modal', {
										title: '图片链接',
										value
									})
								}
							}
						})
					} else {
						this.callback = {
							resolve,
							reject
						}
						let title
						if (type === 'video')
							title = '视频链接'
						else if (type === 'audio')
							title = '音频链接'
						else if (type === 'link')
							title = '链接地址'
						this.$set(this, 'modal', {
							title,
							value
						})
					}
				})
			}
		},
		methods: {
			chooseWord() {
				var that = this;
				uni.chooseMessageFile({
					count: 1, //能选择文件的数量
					type: 'file', //能选择文件的类型,我这里只允许上传文件.还有视频,图片,或者都可以
					success(res) {
						var size = res.tempFiles[0].size;
						var filename = res.tempFiles[0].name;
						var newfilename = filename + "";
						console.log(res.tempFiles[0])
						console.log(newfilename + "-------" + size)
						if (size > 4194304 || newfilename.indexOf(".pdf") == -1) { //我还限制了文件的大小和具体文件类型
							uni.showToast({
								title: '文件大小不能超过4MB,格式必须为pdf！',
								icon: "none",
								duration: 2000,
								mask: true
							})
						} else {
							//后台上传文件到服务器
							const token = uni.getStorageSync('slToken');
							var data = {
								file: res.tempFiles[0].path,
								name: res.tempFiles[0].name
							}
							uni.uploadFile({
								url: baseUrl+'/help/upload',
								filePath: res.tempFiles[0].path,
								name: 'file',
								header: {
									"content-type": "multipart/form-data;charset=UTF-8",
									"Authorization": "Bearer " + token
								},
								success:(res)=>{
									if (res.statusCode === 401) {
										uni.showToast({
											title: '登录超时，重新登录',
											duration: 2000,
											icon: 'none'
										});
									}else{
										let data = JSON.parse(res.data)
										if (data.result == true) {
											uni.showToast({
												title: '上传成功',
												duration: 2000,
												icon: 'none'
											});
										}
									}
								},
								fail:(e)=>{
									console.log(e);
									uni.showToast({
										title: '上传失败',
										duration: 2000,
										icon: 'none'
									});
								}
							});
						}
					}
				});
			},
			ChooseImage() {
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}
					}
				});
			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '召唤师',
					content: '确定要删除这段回忆吗？',
					cancelText: '再看看',
					confirmText: '再见',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
			load() {
				console.log('dom 树加载完毕')
			},
			ready(e) {
				console.log('ready 事件触发：', e)
			},
			imgtap(e) {
				console.log('imgtap 事件触发：', e)
			},
			linktap(e) {
				console.log('linktap 事件触发：', e)
			}, // 调用编辑器接口
			edit(e) {
				this.$refs.article[e.currentTarget.dataset.method]()
			},
			// 清空编辑器内容
			clear() {
				uni.showModal({
					title: '确认',
					content: '确定清空内容吗？',
					success: res => {
						if (res.confirm)
							this.$refs.article.clear()
					}
				})
			},
			// 保存编辑器内容
			save() {

			}
		}
	}
</script>

<style>
	.editor_toolbox {
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 999;
		background-color: #EDEDED;
		display: flex;
		padding: 5px;
		box-sizing: border-box;
		line-height: 1.6;
	}

	@font-face {
		font-family: "iconfont";
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.iconfont {
		flex: 1;
		text-align: center;
		font-family: "iconfont" !important;
		font-size: 22px;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.icon-undo:before {
		content: "\e607";
	}

	.icon-redo:before {
		content: "\e606";
	}

	.icon-img:before {
		content: "\e6e2";
	}

	.icon-video:before {
		content: "\e798";
	}

	.icon-link:before {
		content: "\e60d";
	}

	.icon-text:before {
		content: "\e6ce";
	}

	.icon-clear:before {
		content: "\e637";
	}

	.icon-save:before {
		content: "\e501";
	}

	/* 模态框 */
	.modal {
		position: fixed;
		top: 50%;
		left: 16px;
		right: 16px;
		background-color: #fff;
		border-radius: 12px;
		transform: translateY(-50%);
	}

	.modal_title {
		padding: 32px 24px 16px;
		font-size: 17px;
		font-weight: 700;
		text-align: center;
	}

	.modal_input {
		display: block;
		padding: 5px;
		margin: 0 24px 32px 24px;
		font-size: 14px;
		border: 1px solid #dfe2e5;
	}

	.modal_foot {
		display: flex;
		line-height: 56px;
		font-weight: 700;
		border-top: 1px solid rgba(0, 0, 0, .1);
	}

	.modal_button {
		flex: 1;
		text-align: center;
	}

	/* 蒙版 */
	.mask {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: black;
		opacity: 0.5;
	}
</style>
